import React from 'react';
import logo from "../images/logo(2).png"

function Footer() {
    return (
        <div style={{ background: "black", height: "200px", marginTop: "100px" }}>
            <div className="container">
                <div style={{ float: 'left' }}>
                    <img style={{ width: "150px", marginTop: "30px" }} src={logo} alt="logo" />
                </div>
                <div style={{ float: 'right', color: "white" }}>
                    <h2>1</h2>
                    <p style={{ fontSize: "20px", color: "blue" }}>//</p>
                    <p>  2 10-16</p>
                    <p><span style={{ color: "green", fontSize: "18px" }}> </span>051)727-6616</p>

                </div>

            </div>
        </div>
    )
}

export default Footer